 hidden;\n\t\t\n\t\t.change_inner {\n\t\t\tposition: relative;\n\t\t\tanimation: rotate 10s ease-in-out infinite;\n\t\t}\n\t\t\n\t\t.element {\n\t\t\tdisplay: block;\n\t\t}\n\t\t\n\t}\n\t\n}\n\n@keyframes rotate {\n\t0%, 20% {\n\t\ttransform: translateY(0);\n\t}\n\t25%,45% {\n\t\ttransform: translateY($fontSize * -1.5);\n\t}\n\t50%,70% {\n\t\ttransform: translateY($fontSize * -3);\n\t}\n\t75%,95% {\n\t\ttransform: translateY($fontSize * -4.5);\n\t}\n}","@import \"coreconf\";\n\n#box {\n    border-radius: 15px;\n    background: var(--def-fg-color);\n    color: var(--def-bg-color);\n    font-size: var(--small-font-size);\n    -webkit-user-select: none;\n    -moz-user-select: none;\n    -ms-user-select: none;\n    user-select: none;\n    text-align: justify;\n    padding-left:30px\n\n  } \n\n#description {\n  font-size: var(--normal-font-size);\n  text-align: justify ;\n}\n\n/* Create four equal columns that floats next to each other */\n.column {\n  float: left;\n  width: 50%;\n  padding: 10px 30px 10px 0;\n\n}\n\n\n/* Clear floats */\n.row:after {\n  content: \"\";\n  display: table;\n   clear: both;\n}\n\n/* Responsive layout - makes a two column-layout instead of four columns */\n@media screen and (max-width: 900px) {\n  .column {\n    width: 100%;\n  }\n}\n\n/* Responsive layout - makes the two columns stack on top of each other instead of next to each other */\n@media screen and (max-width: 600px) {\n  .column {\n    width: 100%;\n  }\n}","@import \"tooltip\";\n@import \"logo\";\n@import \"coreconf\";\n@import \"themeswitcher\";\n@import \"carousel\";\n@import \"box\";\n\n* {\n  box-sizing: border-box;\n  padding: 0;\n  margin-top: 0;\n  zoom: 100%;\n}\n\nhtml,\nbody {\n  height: 100%;\n}\n\nbody {\n  background-color: var(--def-bg-color);\n  font-family: var(--def-font), sans-serif;\n  transition: all 0.3s;\n  margin-top: 0;\n  margin-bottom: 0;\n  color: var(--def-fg-color);\n}\n\nheader {\n  width: 100%;\n  display: block;\n  height: 30px;\n  margin-bottom: 2r